.content {
  &__mobile-menu {
    display: none;
    border-radius: 3px;
    border: solid 1px $primary;
    padding: 15px 20px;
    justify-content: space-between;
    margin-top: 20px;
    font-size: 12px;
    font-weight: 600;
    letter-spacing: 1px;
    color: $primary;
    text-decoration: none;
    text-transform: uppercase;
    @include r($tablet) {
      display: flex; }
    i {
      position: relative;
      span {
        display: block;
        width: 20px;
        height: 1.5px;
        background: $primary;
        &:before {
          content: '';
          width: 20px;
          position: absolute;
          top: 6px;
          height: 1.5px;
          background: $primary; }
        &:after {
          content: '';
          width: 20px;
          position: absolute;
          top: 12px;
          height: 1.5px;
          background: $primary; } } } }
  &__description {
    padding: 0 20px;
    margin-bottom: 110px;
    @include r($tablet) {
      padding: 0; }
    h1 {
      font-size: 24px;
      font-weight: 300;
      line-height: 1.08;
      letter-spacing: 7px;
      color: $black-three;
      text-transform: uppercase;
      font-style: normal;
      margin-top: 30px; }
    h2 {
      font-size: 16px;
      font-weight: 300;
      line-height: 1.5;
      letter-spacing: 7px;
      color: $black-three;
      text-align: left;
      display: inline-block;
      margin-top: 40px; }
    p {
      font-size: 15px;
      font-weight: 300;
      line-height: 1.47;
      letter-spacing: 0.3px;
      color: $black-three;
      margin-bottom: 20px;
      span {
        font-weight: normal;
        color: $primary; }
      b {
        font-weight: 500; } }
    ul, ol {
      margin-left: 30px;
      li {
        list-style: disc;
        padding-left: 15px;
        font-weight: 300;
        line-height: 2.45;
        letter-spacing: 0.3px;
        color: $black-three; } }
    ol {
      margin-bottom: 45px;
      li {
        list-style: decimal; } }
    img {
      @include r($tablet) {
        max-width: 385px; }
      @include r($mobile) {
        max-width: 100%;
        width: 100vw;
        padding: 10px 0 !important; } }
    img.content__left {
      float: left;
      padding-right: 30px;
      padding-bottom: 20px; }
    img.content__right {
      float: right;
      padding-left: 30px;
      padding-bottom: 20px; }
    blockquote {
      background-color: $primary;
      padding: 20px 50px;
      margin-bottom: 40px;
      @include r($mobile) {
        padding: 40px 15px; }
      p {
        font-size: 24px;
        font-weight: 300;
        line-height: 1.5;
        letter-spacing: 7px;
        text-align: center;
        color: $white;
        text-transform: uppercase;
        margin-bottom: 0;
        position: relative;
        @include r($tablet) {
          font-size: 18px; }
        @include r($mobile) {
          font-size: 15px;
          font-weight: 300;
          font-style: normal;
          font-stretch: normal;
          line-height: 1.53;
          letter-spacing: 4.3px; }
        &:before {
          content: '\EA0A';
          position: absolute;
          left: -30px;
          font-family: "iconfont";
          @include r($mobile) {
            top: -30px;
            left: 10px; } }
        &:after {
          content: '\EA0B';
          position: absolute;
          right: -30px;
          font-family: "iconfont";
          @include r($mobile) {
            bottom: -30px;
            right: 10px; } } } } }
  &__slider1 {
    max-width: 750px;
    width: 85%;
    left: 50%;
    transform: translate(-50%);
    position: relative;
    @include r($mobile) {
      width: 100%; }
    img {
      @include r($tablet) {
        max-width: 100%;
        width: 100%; } }
    .owl-prev {
      position: absolute;
      left: -50px;
      top: 50%;
      transform: translate(0, -50%);
      @include r($mobile) {
        display: none; }
      span {
        visibility: hidden;
        &:before {
          content: '\EA26';
          font-family: "iconfont";
          visibility: visible;
          color: $primary;
          font-size: 38px;
          opacity: 0.9; } } }
    .owl-next {
      position: absolute;
      right: -50px;
      top: 50%;
      transform: translate(0, -50%);
      @include r($mobile) {
        display: none; }
      span {
        visibility: hidden;
        &:before {
          content: '\EA27';
          font-family: "iconfont";
          visibility: visible;
          color: $primary;
          font-size: 38px;
          opacity: 0.9; } } } }
  .swiper {
    position: relative;
    max-width: 320px;
    width: 100%;
    left: 50%;
    transform: translate(-50%);
    @include r($mobile) {
      max-width: 200px; }
    .swiper__content {
      height: 75px;
      @include r($mobile) {
        height: 60px; }
      .swiper-slide {
        overflow: hidden;
        box-sizing: border-box;
        background: none;
        img {
          width: auto;
          height: 100%; } }
      .owl-thumb-item.swiper-slide.active {
        border: solid 2.9px $primary; } }
    .content-prev {
      left: -50px;
      @include r($tablet) {
        display: block; } }
    .content-next {
      right: -50px;
      @include r($tablet) {
        display: block; } } }
  .table {
    margin-bottom: 50px;
    padding-bottom: 30px;
    &::-webkit-scrollbar {
      height: 6px; }
    &::-webkit-scrollbar-track {
      background-color: #e5e5e9;
      border-radius: 20px; }
    &::-webkit-scrollbar-thumb {
      -webkit-border-radius: 20px;
      border-radius: 20px;
      background-color: #c8a05f;
      &:hover {
        background-color: #56999f; } }
    &::-webkit-scrollbar {
      width: 1px; }
    @include r($tablet) {
      overflow-y: hidden;
      width: auto; }
    table {
      @include r($tablet) {
        min-width: 915px; }
      tr {
        &:nth-child(2n) {
          background: rgba(200, 160, 95, 0.21); }
        td, th {
          font-weight: 300;
          text-align: left;
          color: #131313;
          padding: 25px 0;
          padding-left: 25px;
          border: none; }
        th {
          letter-spacing: 0.3px;
          font-weight: 400; } } } }
  &__files, &__links-1, &__links-2 {
    margin-bottom: 50px;
    a {
      display: flex;
      text-decoration: none;
      border-radius: 4px;
      border: solid 1px #c8a05f;
      padding: 15px;
      padding-right: 5px;
      transition: all 0.3s;
      min-height: 105px;
      margin-bottom: 15px;
      &:hover {
        background: #c8a05f;
        i, b, p, span {
          color: $white; } }
      .content__files__description {
        display: flex;
        flex-direction: column; }
      i {
        font-size: 33px;
        font-size: 55px;
        margin-right: 15px;
        display: block;
        color: #c8a05f;
        transition: all 0.3s; }
      p {
        font-size: 14px;
        line-height: 1.4;
        letter-spacing: 0.2px;
        font-weight: normal;
        color: #131313;
        margin-bottom: 0;
        transition: all 0.3s; }
      span {
        font-size: 12px;
        letter-spacing: 0.3px;
        text-align: left;
        color: #131313;
        padding: 10px 0;
        transition: all 0.3s; }
      b {
        font-size: 14px;
        font-weight: normal;
        font-style: normal;
        letter-spacing: 0.3px;
        text-align: left;
        color: #c8a05f;
        transition: all 0.3s; } } }
  &__links-1 {
    a {
      i {
        font-size: 37px; } } }
  &__links-2 {
    a {
      align-items: center; } } }
