.pagination {
  @include center;
  a {
    width: 48px;
    height: 48px;
    margin-right: 10px;
    border-radius: 3px;
    border: solid 1px $primary;
    text-decoration: none;
    color: $primary;
    font-size: 19px;
    letter-spacing: 1px;
    @include center;
    @include tr;
    &.is-active, &:hover {
      background: $primary;
      color: $white; }
    &.mobile-none {
      @include r($mobile) {
        display: none; } } }
  span {
    height: 48px;
    font-size: 50px;
    margin: 0 15px 0 5px;
    @include center;
    i {
      width: 7px;
      height: 7px;
      background: #dac39f;
      border-radius: 50%;
      display: block;
      &:not(:last-child) {
        margin-right: 8px; } } }
  .prev, .next {
    border: none;
    i {
      font-size: 25px; } } }
