.product {
  &__card {
    width: 260px;
    background: $white;
    height: 100%;
    display: block;
    box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.17);
    border-radius: 2px;
    margin-left: 10px;
    overflow: hidden;
    @include tr;
    @include r($tablet) {
      width: 220px;
      margin-left: 0; }
    @include r($mobile) {
      width: 100%; }
    &:hover {
      box-shadow: 0 10px 20px rgba(0,0,0,.2); }
    &__header {
      position: relative;
      &__image {
        width: 100%;
        height: 220px;
        overflow: hidden;
        display: block;
        img {
          height: 100%;
          display: block;
          left: 50%;
          position: absolute;
          transform: translateX(-50%); }
        &--no-stock {
          position: absolute;
          height: 40px;
          width: 100%;
          background-color: rgba(#383838, 0.35);
          border-radius: 1.6px;
          bottom: 0;
          @include center;
          color: $white;
          font-size: 15px;
          font-weight: 500;
          letter-spacing: 1.1px;
          text-transform: uppercase; } }
      .top {
        position: absolute;
        top: 15px;
        font-size: 14px;
        color: $white;
        width: 60px;
        height: 30px;
        text-transform: uppercase;
        letter-spacing: 1.1px;
        border-radius: 1.6px;
        @include center;
        @include r($tablet) {
          width: 43px;
          height: 22px;
          font-size: 11px;
          letter-spacing: 0.9px; } }
      &__hits {
        left: 20px;
        background: $green;
        @include center;
        @include r($tablet) {
          left: 12px; } }
      &__sale {
        top: 20px;
        left: 85px;
        background: $red;
        @include r($tablet) {
          left: 60px; } }
      &__wishlist {
        position: absolute;
        top: 15px;
        right: 15px;
        i {
          font-size: 23px;
          color: $red;
          @include r($tablet) {
            font-size: 18px;
            top: 2px;
            position: relative; } } } }
    &__content {
      padding: 30px;
      text-align: center;
      a {
        text-decoration: none;
        &:hover {
          text-decoration: underline; } }
      h3 {
        font-size: 18px;
        text-transform: uppercase;
        line-height: 1.33;
        letter-spacing: 1px;
        color: rgba($black-three, 0.9);
        font-weight: 400;
        margin-bottom: 10px;
        @include r($tablet) {
          font-size: 13px; } }
      span {
        display: block;
        font-weight: 400;
        font-size: 14px;
        color: rgba($black-three, 0.8);
        margin-bottom: 15px;
        @include r($tablet) {
          font-size: 10px; } }
      &__price {
        div {
          height: 45px;
          @include center;
          flex-direction: column; }
        s {
          color: #acacac;
          margin-right: 5px;
          font-weight: 500;
          letter-spacing: 0.6px;
          display: block;
          @include r($tablet) {
            font-size: 11px; } }
        p {
          color: $green;
          font-weight: normal;
          display: inline-block;
          margin-bottom: 10px;
          @include r($tablet) {
            font-size: 11px;
            font-weight: 600; } }
        h4 {
          margin: 10px;
          font-size: 24px;
          font-weight: bold;
          letter-spacing: 1.1px;
          color: #393939;
          height: 30px;
          @include r($tablet) {
            font-size: 18px;
            letter-spacing: 0.8px; } } }
      .btn {
        @include center;
        height: 45px;
        padding: 0;
        width: 100%; }
      &.no-stock {
        p {
          color: $primary; }
        h4 {
          font-size: 21px;
          color: #acacac;
          font-weight: 500; } } } } }



.catalog .product__card {
  width: 211px;
  margin-left: 0;
  height: initial;
  margin-bottom: 20px;
  @include r($tablet) {
    width: 100%;
    margin-bottom: 4px; }
  &__header {
    &__image {
      height: 200px;
      @include r($mobile) {
        height: 125px; } }
    .top {
      top: 12px;
      width: 40px;
      height: 20px;
      font-size: 11px;
      @include r($mobile) {
        top: 9px;
        width: 35px;
        height: 18px;
        font-size: 10px; } }
    &__hits {
      left: 12px;
      @include r($mobile) {
        left: 10px; } }
    &__sale {
      left: 60px;
      @include r($mobile) {
        left: 50px; } }
    &__wishlist {
      top: 12px;
      right: 12px;
      i {
        font-size: 20px; }
      @include r($mobile) {
        top: 9px;
        right: 10px; } } }
  &__content {
    @include r($mobile) {
      padding: 15px; }
    h3 {
      @include r($mobile) {
        font-size: 10px; } }
    span {
      @include r($mobile) {
        margin-bottom: 0; } }
    &__price {
      div {
        height: 30px;
        @include r($mobile) {
          flex-direction: row;
          p {
            margin-bottom: 0;
            margin-right: 5px;
            font-size: 9px; }
          s {
            font-size: 9px; } } }
      h4 {
        font-size: 18px;
        @include r($mobile) {
          font-size: 14px;
          margin: 0; } } } }
  .no-stock {
    h4 {
      font-size: 15px !important;
      @include r($mobile) {
        font-size: 13px !important; } } }
  .btn {
    font-size: 11px;
    height: 35px;
    @include r($mobile) {
      height: 30px;
      font-size: 10px; } } }

