.catalog {
  .row {
    height: inherit; }
  .col-xs-6 {
    padding: 0 2px; }
  h1 {
    font-size: 24px;
    font-weight: 300;
    line-height: 1.08;
    letter-spacing: 7px;
    color: $black-three;
    text-transform: uppercase;
    margin-bottom: 25px;
    position: relative;
    margin-top: 30px;
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    @include r($mobile) {
      flex-direction: column;
      align-items: flex-start; }
    span {
      font-size: 15px;
      font-weight: 500;
      letter-spacing: 0.2px;
      text-align: left;
      color: rgba($black-three, 0.5);
      margin-left: 15px;
      text-transform: none;
      @include r($mobile) {
        margin-left: 0;
        font-size: 14px;
        margin-top: 10px; } } }

  &__sort {
    padding: 14px 18px;
    background: #e5e5e5;
    display: inline-block;
    border-radius: 3px;
    position: relative;
    float: right;
    height: 50px;
    @include r($tablet) {
      width: 100%; }
    @include r($mobile) {
      padding: 14px; }
    &:before {
      content: $icon-filter-arrow;
      color: $primary;
      font-size: 10px;
      @extend %icon;
      position: absolute;
      right: 15px;
      top: 50%;
      transform: translateY(-50%); }
    p {
      font-size: 14px;
      font-weight: 500;
      color: #7a7a7a;
      margin-right: 15px;
      display: inline-block;
      @include r($mobile) {
        font-size: 12px; } }
    select {
      background: none;
      border: none;
      -webkit-appearance: none;
      color: $primary;
      font-size: 14px;
      font-weight: 500;
      margin-right: 20px;
      text-align: right;
      @include r($mobile) {
        font-size: 12px;
        margin-right: 0; } } }
  &__brands {
    &__row {
      h3 {
        font-size: 16px;
        line-height: 1.5;
        letter-spacing: 7px;
        text-align: left;
        color: rgba($black-three, 0.8);
        margin: 20px 0; } } }
  &__collections {
    &__row {
      h3 {
        font-size: 16px;
        font-weight: 300;
        line-height: 1.5;
        letter-spacing: 1px;
        color: $black-three;
        text-transform: uppercase;
        margin: 20px 0;
        b {
          font-weight: 500; } } } }
  &__not {
    @include center;
    flex-direction: column;
    h2 {
      margin-top: 100px;
      font-size: 25px;
      font-weight: 500;
      color: rgba($black-three, 0.4);
      text-transform: none;
      letter-spacing: normal;
      margin-bottom: 70px;
      @include r($tablet) {
        margin-top: 20px; } }
    p {
      font-size: 16px;
      color: $black-three;
      font-weight: 500;
      line-height: 1.2;
      margin-bottom: 30px;
      @include r($mobile) {
        text-align: center;
        br {
          display: none; } } }
    &__phone {
      font-size: 32px;
      font-weight: 300;
      letter-spacing: 0.8px;
      text-decoration: none;
      color: $black-two;
      border-bottom: 1px dashed $black-two;
      margin-bottom: 15px; }
    span {
      font-size: 10px;
      text-transform: uppercase;
      color: rgba($black-three, 0.5);
      letter-spacing: 1px; } }
  &__filter {
    &__mobile {
      margin-top: 20px;
      .btn {
        width: 100%;
        justify-content: space-between;
        padding: 0 20px;
        @include r($mobile) {
          margin-bottom: 10px; } } } }
  &__more {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 20px;
    margin-bottom: 60px;
    a {
      width: 285px;
      margin-bottom: 15px; }
    span {
      font-size: 14px;
      color: rgba($black-three, 0.5); } } }


